import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface, PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {routing} from './pages.routing';
import {PagesComponent} from './pages.component';
import {HeaderComponent} from '../theme/components/header/header.component';
import {FooterComponent} from '../theme/components/footer/footer.component';
import {SidebarComponent} from '../theme/components/sidebar/sidebar.component';
import {VerticalMenuComponent} from '../theme/components/menu/vertical-menu/vertical-menu.component';
import {HorizontalMenuComponent} from '../theme/components/menu/horizontal-menu/horizontal-menu.component';
import {BreadcrumbComponent} from '../theme/components/breadcrumb/breadcrumb.component';
import {BackTopComponent} from '../theme/components/back-top/back-top.component';
import {UserMenuComponent} from '../theme/components/user-menu/user-menu.component';
import {BlankComponent} from './blank/blank.component';
import {SearchComponent} from './search/search.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {UserManagementModule} from './users/user-management.module';
import {FilterPipe} from '../pipes/filter.pipe';
import {LoaderModule} from '../shared/loader/loader.module';
import {CustomErrorModule} from '../shared/custom-error/custom-error.module';
import {SurveyModule} from '../shared/survey/survey.module';
import {NotificationsComponent} from './notifications/notifications.component';
import {SaveNotificationComponent} from './notifications/save-notification/save-notification.component';
import {NotifierModule} from 'angular-notifier';
import {AppNotifierService} from '../services/app-notifier.service';
import { MyProfileComponent } from './my-profile/my-profile.component';
import {CapitalizePipe} from '../pipes/capitalize.pipe';
import {TrimPipe} from '../pipes/trim.pipe';
import {DatePipePipe} from '../pipes/date.pipe';
import {VendorsModule} from './vendors/vendors.module';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
    suppressScrollX: true
};


@NgModule({
  imports: [
    CommonModule,
    PerfectScrollbarModule,
    routing,
    FormsModule,
    ReactiveFormsModule,
    UserManagementModule,
    LoaderModule,
    CustomErrorModule,
    SurveyModule,
    NotifierModule,
    VendorsModule,
    HttpClientModule
  ],
    declarations: [
        PagesComponent,
        HeaderComponent,
        FooterComponent,
        SidebarComponent,
        VerticalMenuComponent,
        HorizontalMenuComponent,
        BreadcrumbComponent,
        BackTopComponent,
        UserMenuComponent,
        BlankComponent,
        SearchComponent,
        FilterPipe, CapitalizePipe, TrimPipe, DatePipePipe,
        NotificationsComponent,
        SaveNotificationComponent,
        MyProfileComponent
    ],
    exports: [
        FilterPipe, CapitalizePipe, TrimPipe, DatePipePipe,
    ],
    providers: [
        {
            provide: PERFECT_SCROLLBAR_CONFIG,
            useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
        },
        AppNotifierService,
    ]
})
export class PagesModule {
}
