import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';
// import {TimeAgoPipe} from 'time-ago-pipe';
import {FormBuilder, FormGroup} from '@angular/forms';
import {NotificationsService} from '../../services/notifications.service';
import {Constants} from '../../app.constants';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-notifications',
    templateUrl: './notifications.component.html',
    styleUrls: ['./notifications.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class NotificationsComponent implements OnInit {
    // public notifications: Notification[] = [];
    // public notification: Notification;
    public notifications: Array<Notification>;
    public searchText: string;
    public p: any;
    Notification: any;
    toggle = false;
    selectedAll: any;
    selectedNotification: Notification[];
    loading: boolean;
    errorMessage: any;
    public placeHolderMessage = '';
    role = localStorage.getItem('userRole')
    public notification: any;
    current_page: number;
    data: Notification[];
    from: number;
    last_page: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    to: number;
    total: number;
    title: string;
    body: string;
    created_at: string;
    searchForm: FormGroup;
    searching = false;
    perPage = 15;

    url: string;

    constructor(public notificationsService: NotificationsService,
                private route: ActivatedRoute, private _Location: Location,
                private router: Router, private formBuilder: FormBuilder) {
    }

    ngOnInit() {
        this.buildForm();
        this.submitSearch()
    }

    private buildForm() {
        this.searchForm = this.formBuilder.group({
            fromDate: [''],
            toDate: [''],
            status: ['']
        });
    }

    submitSearch() {
        const fromDate = this.searchForm.get('fromDate').value;
        const toDate = this.searchForm.get('toDate').value;
        const status = this.searchForm.get('status').value;

        let param = '';

        if (fromDate) {
            if (param) {
                param += '&';
            }
            param += 'from=' + this.getTextFromDate(fromDate);
        }

        if (toDate) {
            if (param) {
                param += '&';
            }
            param += 'to=' + this.getTextFromDate(fromDate);
        }

        if (status) {
            if (param) {
                param += '&';
            }
            param += 'status=' + status;
        }

        if (param) {
            param += '&';
        }
        param += 'per_page=' + this.perPage;

        if (param) {
            param = '?' + param;
        }

        this.search('', param);
    }

    search(url, param) {
        this.searching = true;
        this.errorMessage = '';

        this.notifications = [];
        this.last_page = -1;

        if (url === '') {
            url = Constants.url + 'notifications' + param;
        }
        this.notificationsService.getNotifications(url).subscribe((data) => {
            const arr = ((typeof data.data) !== 'undefined' && data.data.length > 0);
            if (arr) {
                data.data.forEach(d => d.selected = false);
            }
            this.notifications = data.data;
            this.current_page = data.current_page;
            this.from = data.from;
            this.last_page = data.last_page;
            this.next_page_url = data.next_page_url;
            this.path = data.path;
            this.prev_page_url = data.prev_page_url;
            this.per_page = data.per_page;
            this.to = data.to;
            this.total = data.total;

            this.searching = false;

            // console.log(this.notifications)
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.searching = false;
        })
    }

    deleteNotification(id) {
        Swal.fire({
            title: 'Are you sure?',
            text: 'You won\'t be able to revert this!',

        }).then(willDelete => {
            if (willDelete.value) {
                this.notificationsService.deleteNotification(id).subscribe((status: boolean) => {
                    if (status) {
                        Constants.succeeded('Notification was deleted!')
                        window.setTimeout(function () {
                            location.reload();
                        }, 2000)
                    } else {
                        Constants.error('Unable to delete Notification')
                    }
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                });
            }
        })
    }

    private getTextFromDate(date: any) {
        if (typeof date === 'object') {
            return date.year + '-' + date.month + '-' + date.day;
        }
        return date.toString();
    }
}
