import {Component, EventEmitter, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {ISurveyReport, ISurveyStructure} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {SearchComponent} from '../../../pages/search/search.component';
import {MarketSurveyService} from '../../../services/market-survey.service';
import {SurveyService} from '../../../services/survey.service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
    selector: 'app-view-surveylist',
    templateUrl: './view-surveylist.component.html',
    styleUrls: ['./view-surveylist.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class ViewSurveylistComponent implements OnInit {

    model: any = {};
    existingLocal = [];
    ExistingLocalArray = [];
    structure = this.ExistingLocalArray;
    public NewSurveyForm: FormGroup;
    errorMessage: string;
    loading: boolean;
    public id: number;
    public array: any [];
    subtotal: number;
    markup: 0;
    url: string;
    saving = false;

    @Output() updateInSurvey = new EventEmitter();

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private oldSurveyService: SurveyService,
                private surveyService: MarketSurveyService,
                private _searchComponent?: SearchComponent) {
    }

    ngOnInit() {
        this.buildForm();
        this.url = Constants.url;
    }

    get currentMarketSurvey(): ISurveyReport {
        return this.surveyService.getCurrentLocalSurvey();
    }

    buildForm() {
        this.NewSurveyForm = this.formBuilder.group({
            title: [this.currentMarketSurvey.title, Validators.required],
            description: [this.currentMarketSurvey.description, Validators.required],
        })
    }

    removeItem(good) {
        this.surveyService.removeFromCurrentSurvey(good);
        // this._searchComponent.updateInSurveyStatus();
        this.updateInSurvey.emit(good);
    }

    updateSurveyData() {
        const title = this.NewSurveyForm.get('title').value,
            description = this.NewSurveyForm.get('description').value;
        this.currentMarketSurvey.title = title;
        this.currentMarketSurvey.description = description;
        this.surveyService.updateLocalSurveyDB();
    }

    onSubmit({value, valid}: {
        value: ISurveyReport, valid: boolean
    }) {
        this.errorMessage = '';
        if (value || !this.currentMarketSurvey.structure_.length) {
            this.saving = true;
            if (this.currentMarketSurvey.title && this.currentMarketSurvey.structure_) {
                const survey = this.prepareToSave();
                this.surveyService.createNewSurveyRecord(survey).subscribe((res) => {
                        this.saving = false;
                        this.surveyService.reset();
                        this.currentMarketSurvey.title = '';
                        this.currentMarketSurvey.description = '';
                        this.surveyService.updateLocalSurveyDB();
                        document.getElementById('survey-modal-dismiss').click();
                        // this._searchComponent.updateInSurveyStatus();
                        Constants.succeeded('Survey report created successfully');
                        this.NewSurveyForm.patchValue({
                            title: '',
                            description: '',
                        });
                    }, (err) => {
                        this.saving = false;
                        Constants.handleError(err, this.router, true);
                        document.getElementById('survey-modal-dismiss').click();
                    }
                );
            }
        } else {
            if (!this.currentMarketSurvey.structure_.length) {
                Constants.error('No items added to the current survey report.');
            }
        }
    }

    private prepareToSave() {
        const newStructure: ISurveyStructure[] = [];

        this.currentMarketSurvey.structure_.forEach(s => {
            newStructure.push({
                mark_up: s.mark_up,
                item: {
                    sku: s.item.sku,
                    full_description: s.item.full_description,
                    price: s.item.price,
                    price_formatted: s.item.price_formatted,
                    vendor: s.item.vendor,
                    picture: s.item.picture,
                    warehouse: s.item.warehouse,
                    detail: s.item.detail,
                    properties: s.item.properties,
                    id: s.item.id,
                    all_good_id: s.item.all_good_id
                }
            });
        });
        return {
            title: this.currentMarketSurvey.title,
            description: this.currentMarketSurvey.description,
            structure: JSON.stringify(newStructure),
        };
    }

}
