import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {Grades, IInternalUser, IMinistry, IRoles} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {UserDataService} from '../../../services/user.service';
import {RolesService} from '../../../services/roles.service';
import {GradeService} from '../../../services/grade.service';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../data';
import {Location} from '@angular/common';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class EditUserComponent implements OnInit {

  @Input() user: any = null;
  public userForm: FormGroup;
  internalUser: any = false;
  roles: IRoles[] = [];
  errorMessage: any;
  model: any = {};
  loading = false;
  lockLoading: boolean;
  showHide: boolean;
  pageLoading: boolean;
  onSuccess: any;
  frontendUrl = window.location.hostname;
  institutions: IMinistry[] = [];
  parastatals: IMinistry[] = [];
  grades: Grades[] = [];
  isCurrentUser = true;

  userId = -1;
  userData: any = {};
  updating = false;
  isSelf = false;

  constructor(private userDataService: UserDataService,
              private rolesService: RolesService,
              private gradesService: GradeService,
              private route: ActivatedRoute,
              private formBuilder: FormBuilder,
              private router: Router,
              public authService: AuthService,
              private _location: Location) {
  }

  can(permission) {
    return AuthService.can(permission);
  }

  get permissions() {
    return AppData.permissions;
  }

  ngOnInit() {
    this.userId = this.route.snapshot.params['id'];
    if (this.user && this.user.id) {
      this.userId = this.user.id;
      this.isSelf = true;
    }
    this.getInternalUser();

    const user = AuthService.getAuthenticatedUser();
    this.isCurrentUser = user && (this.userId.toString() === user.i.toString());

    this.buildForm();
    this.showHide = false;
  }

  back() {
    this._location.back();
  }

  buildForm() {
    const controlConfig = {
      first_name: [this.internalUser.first_name, Validators.compose([Validators.required, Validators.maxLength(255)])],
      last_name: [this.internalUser.last_name, Validators.compose([Validators.required, Validators.maxLength(255)])],
      email: [this.internalUser.email, Validators.compose([Validators.required, Validators.email])],
      phone: [this.internalUser.phone, Validators.required],
      username: [{value: this.internalUser.username, disabled: true}, Validators.required],
      password: ['', Validators.compose([])],
      institution_id: [this.internalUser.institution_id],
      grade_id: [this.internalUser.grade_id],
      parastatal_id: [this.internalUser.parastatal_id],
      job_title: [this.internalUser.job_title],
      employed_date: [this.internalUser.employed_date],
      frontend_url: [this.frontendUrl],
      role: null
    };

    controlConfig.role = [this.userData.role, Validators.required];
    this.userForm = this.formBuilder.group(controlConfig);
  }

  get userRole() {
    const userData = AuthService.getAuthenticatedUser();
    return userData ? userData.r.toLowerCase() : '';
  }

  getInstitutions() {
    this.userDataService.getInstitutions().subscribe((ministries: IMinistry[]) => {
      this.institutions = ministries;
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
    })
  }

  getGrades() {
    this.gradesService.getGrades().subscribe((grades: Grades[]) => {
      this.grades = grades;
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
    })
  }

  changeParastatals(value) {
    this.parastatals = [];
    if (value) {
      for (let x = 0; x < this.institutions.length; x++) {
        if (this.institutions[x].id === value) {
          this.parastatals = this.institutions[x].parastatals;
          break;
        }
      }
    }
  }

  getInternalUser() {
    this.pageLoading = true;
    this.userDataService.getInternalUserData(this.userId).subscribe((data: any) => {
        this.internalUser = data.user.internal_user;
        this.userData = data.user;
        this.roles = data.roles;
        this.grades = data.grades;
        this.institutions = data.institutions;
        this.pageLoading = false;
        this.userForm.patchValue({
          first_name: this.userData.first_name,
          last_name: this.userData.last_name,
          email: this.userData.email,
          phone: this.userData.phone,
          username: this.userData.username,
          password: '',
          role: this.userData.role,
          institution_id: this.internalUser.institution_id,
          grade_id: this.internalUser.grade_id,
          parastatal_id: this.internalUser.parastatal_id,
          job_title: this.internalUser.job_title,
          employed_date: this.internalUser.employed_date
        });

        this.changeParastatals(this.internalUser.institution_id);
      }, (err) => {
        this.errorMessage = Constants.handleError(err, this.router);
        this.pageLoading = false;
      }
    );
  }

  toggleLock() {
    this.showHide = !this.showHide;
  }

  onSubmit({value, valid}: { value: IInternalUser, valid: boolean }) {
    if (this.internalUser) {
      this.updating = true;
      this.userDataService.updateInternalUserData(this.userId, value).subscribe((data) => {
          if (data) {
            Constants.succeeded('User profile updated successfully!');
          }
          this.updating = false;
        }, (err) => {
          Constants.handleError(err, this.router, true);
          this.updating = false;
        }
      )
    }
  }

  onDelete() {
    Swal.fire({
      title: 'Confirmation',
      text: 'Do you want to delete this user?',
      type: 'question',
      cancelButtonText: 'Cancel',
      showCancelButton: true,
      confirmButtonText: 'Yes Delete',
      showConfirmButton: true
    }).then(willDelete => {
      if (willDelete.value) {
        this.userDataService.deleteUser(this.internalUser.user_id).subscribe((status: boolean) => {
          if (status) {
            Swal.fire('Deleted!', 'User profile deleted successfully!', 'success');
            this.router.navigate(['/app/users/list']);
          } else {
            Swal.fire('Error', 'Unable to delete user', 'error');
          }
        }, (err) => {
          Constants.handleError(err, this.router, true);
        })
      }
    })
  }

  onLock() {
    this.lockLoading = true;
    this.userDataService.lockUser(this.userId, this.model.reason).subscribe((reason) => {
      if (reason) {
        Constants.succeeded('User account status changed!');
      }
      this.lockLoading = false;
    }, (err) => {
      Constants.handleError(err, this.router, true)
      this.lockLoading = false;
    });
  }

}
