import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Grades, IMinistry, IRoles, IUser} from '../../../interfaces/interfaces';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {UserDataService} from '../../../services/user.service';
import {RolesService} from '../../../services/roles.service';
import {GradeService} from '../../../services/grade.service';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../../services/auth.service';
import {Constants} from '../../../app.constants';
import {Location} from '@angular/common';
import {AppData} from '../../../data';

@Component({
    selector: 'app-profile',
    templateUrl: './profile.component.html',
    styleUrls: ['./profile.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class ProfileComponent implements OnInit {

    public userForm: FormGroup;
    userData: IUser = null;
    errorMessage: any;
    model: any = {};
    loading = false;
    lockLoading: boolean;
    showHide: boolean;
    pageLoading: boolean;
    onSuccess: any;
    deleteMessageEnabled: boolean;
    frontendUrl = window.location.hostname;
    grades: Grades[] = [];
    // userRole: string = localStorage.getItem('userRole');
    isCurrentUser = true;

    constructor(private userDataService: UserDataService,
                private rolesService: RolesService,
                private gradesService: GradeService,
                private route: ActivatedRoute,
                private formBuilder: FormBuilder,
                private _Location: Location,
                private router: Router,
                public authService: AuthService) {
    }

    ngOnInit() {
        const user_id = this.route.snapshot.params['id'];
        this.getUserProfile(user_id);

        this.buildForm();
        this.showHide = false;
    }

    back() {
        this._Location.back();
    }

    get isVendor() {
      return (Constants.getAuthenticatedUser() || {}).r === AppData.roles.vendor;
    }

    buildForm() {
    }

    getUserProfile(username) {
        this.pageLoading = true;
        this.userDataService.getUserProfile(username).subscribe((userData: any) => {
                this.userData = userData;
                this.pageLoading = false;
                this.isCurrentUser = this.userData.id === (Constants.getAuthenticatedUser()).i;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.pageLoading = false;
            }
        );
    }

    toggleLock() {
        this.showHide = !this.showHide;
    }

    get permissions() {
        return AppData.permissions;
    }

    can (permission) {
        return AuthService.can(permission);
    }

    onLock() {
        this.lockLoading = true;
        this.userDataService.lockUser(this.userData.id, this.model.reason).subscribe((reason) => {
            if (reason) {
                Constants.succeeded('User account status changed!');
            }
            this.lockLoading = false;
        }, (err) => {
            Constants.handleError(err, this.router, true)
            this.lockLoading = false;
        });
    }

}
