import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {UserDataService} from '../../services/user.service';
import {Constants} from '../../app.constants';
import {HttpClient, HttpEventType} from '@angular/common/http';
import {Router} from '@angular/router';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ChangePasswordComponent implements OnInit {
  form: FormGroup;
  updating = {
    status: false
  };

  @Input() userId = '';
  @Input() isSelf = '';

  constructor(private fb: FormBuilder,
              private userService: UserDataService,
              private http: HttpClient, private router: Router) {
  }

  ngOnInit() {
    this.form = this.fb.group({
      old_password: [''],
      password: [''],
      password_confirmation: [''],
    });
  }

  updatePassword() {
    this.updating.status = true;

    let url = 'users/change-password/' + this.userId;

    if (this.isSelf) {
      url = 'change-password';
    }

    this.userService.changePassword(url, this.form.value).subscribe((response: any) => {
      Constants.succeeded(response.message, 'Successful');
      this.updating.status = false;
    }, (error) => {
      this.updating.status = false;
      Constants.handleError(error, this.router, true);
    });
  }
}
