import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router'
import 'rxjs/add/operator/map';
import {Constants} from '../../../app.constants';
import {Subject} from 'rxjs/Subject';
import {InternalUser} from '../../../interfaces/interfaces';
import {NotificationsService} from '../../../services/notifications.service';

@Component({
    selector: 'app-save-notification',
    templateUrl: './save-notification.component.html',
    styleUrls: ['./save-notification.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class SaveNotificationComponent implements OnInit {
    public internalusers: InternalUser[] = [];
    public internaluser: InternalUser;
    public notifications: InternalUser[] = [];
    public notification: InternalUser;
    public selected = [];
    public NewNotificationForm: FormGroup;
    errorMessage: string;
    loading: boolean;
    results: Object;
    ereqLoading: boolean;
    searchTerm$ = new Subject<string>();


    searchResult = [];

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private notificationsService: NotificationsService) {

        this.ereqLoading = true;
        this.notificationsService.search(this.searchTerm$)

            .subscribe(results => {
                this.ereqLoading = false;
                // console.log(results)
                this.results = results;
            });
    }

    ngOnInit() {
        this.buildForm();
        this.getUsers();
    }


    public getUsers(): void {
        this.notificationsService.getUsers().subscribe((internalusers: InternalUser[]) => {
                this.internalusers = internalusers;
            }
        );
    }


    buildForm() {
        this.NewNotificationForm = this.formBuilder.group({
            title: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            body: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            users: this.formBuilder.array([]),
        })
        // this.addDetails();

    }

    onChange(internalusers: string, isChecked: boolean) {
        const userFormArray = <FormArray>this.NewNotificationForm.controls.users;
        if (isChecked) {
            userFormArray.push(new FormControl(internalusers));
        } else {
            const index = userFormArray.controls.findIndex(x => x.value.toString() === internalusers)
            userFormArray.removeAt(index);
        }
    }

    onSubmit({value, valid}: { value: Notification, valid: boolean }) {
        this.loading = true;
        if (value) {
            //    console.log(value)
            this.notificationsService.addNotification(value).subscribe((notifications: Notification) => {
                    if (notifications) {
                        this.router.navigate(['/app/notifications'])
                    } else {
                        this.loading = false;
                    }
                }, (err) => {
                    this.errorMessage = Constants.handleError(err, this.router);
                    this.loading = false;
                }
            )
        }

    }


}
