import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {CreateVendorComponent} from './create-vendor/create-vendor.component';
import {VendorsListComponent} from './vendors-list/vendors-list.component';
import {EditVendorComponent} from './edit-vendor/edit-vendor.component';
import {RouterModule, Routes} from '@angular/router';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../shared/loader/loader.module';
import {ChangePasswordModule} from '../../shared/change-password/change-password.module';

export const routes: Routes = [
    {path: 'create', component: CreateVendorComponent},
    {path: 'edit/:id', component: EditVendorComponent},
    {path: '', redirectTo: 'list', pathMatch: 'full'},
    {path: 'list', component: VendorsListComponent}
];

@NgModule({
  declarations: [CreateVendorComponent, VendorsListComponent, EditVendorComponent],
  exports: [
    EditVendorComponent
  ],
  imports: [
    CommonModule,
    PerfectScrollbarModule,
    RouterModule.forChild(routes),
    FormsModule, CustomErrorModule, LoaderModule,
    ReactiveFormsModule, ChangePasswordModule
  ]
})
export class VendorsModule {
}
