import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {UserDataService} from '../../../services/user.service';
import {Router} from '@angular/router';
import {FormControl} from '@angular/forms';
import {IInternalUser, IRoles, PaginatedLog} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../data';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class UserListComponent implements OnInit {

  internalUsers: IInternalUser[] = [];
  loading: boolean;
  role = localStorage.getItem('userRole');
  errorMessage: any;
  public type = 'list';
  pUsers: PaginatedLog;
  from: number;
  current_page: number;
  last_page: number;
  total: number;
  to: number;
  next_page_url: string;
  path: string;
  per_page: number;
  prev_page_url: string;
  queryString: '';
  model: any = {};
  create_user: boolean;
  roles: IRoles[] = [];
  searchField: FormControl;
  userPermissions = [];


  constructor(private userDataService: UserDataService,
              private router: Router) {
  }

  ngOnInit() {
    this.searchField = new FormControl();
    if (AuthService.can(this.permissions.create_internal_users)) {
      this.userPermissions.push(this.permissions.create_internal_users);
    }
    if (AuthService.can(this.permissions.update_internal_users)) {
      this.userPermissions.push(this.permissions.update_internal_users);
    }
    if (AuthService.can(this.permissions.delete_users)) {
      this.userPermissions.push(this.permissions.delete_users);
    }

    if (AuthService.can(this.permissions.edit_roles)) {
      this.userPermissions.push(this.permissions.edit_roles);
    }
    this.submitSearch();
  }

  can(permission) {
    return this.userPermissions.indexOf(permission) > -1;
  }

  get permissions() {
    return AppData.permissions;
  }

  submitSearch(perPage: any = 15) {
    this.loading = true;
    let fromDate;
    let toDate;
    if (this.model.hasOwnProperty('from')) {
      fromDate = this.model.from.year + '-' + this.model.from.month + '-' + this.model.from.day
    }
    if (this.model.hasOwnProperty('to')) {
      toDate = this.model.to.year + '-' + this.model.to.month + '-' + this.model.to.day
    }

    const searchParam = [];
    if (this.queryString) {
      searchParam.push('search=' + this.queryString);
    }

    if (fromDate) {
      searchParam.push('from=' + fromDate);
    }
    if (toDate) {
      searchParam.push('to=' + toDate);
    }

    if (perPage) {
      searchParam.push('per-page=' + perPage);
    }

    if (searchParam.length) {
      this.searchUsers('', searchParam.join('&'));
    } else {
      this.searchUsers('', '');
    }
  }

  searchUsers(url, params) {
    this.internalUsers = [];
    this.next_page_url = null;
    this.prev_page_url = null;
    this.last_page = -1;
    this.userDataService.searchUsers(url, params)
      .subscribe((data: any) => {
        this.internalUsers = data.data;
        this.current_page = data.current_page;
        this.from = data.from;
        this.last_page = data.last_page;
        this.next_page_url = data.next_page_url;
        this.path = data.path;
        this.prev_page_url = data.prev_page_url;
        this.per_page = data.per_page;
        this.to = data.to;
        this.total = data.total;
        this.loading = false;
      }, (err) => {
        this.loading = false;
        const error = Constants.handleError(err, this.router);
        this.errorMessage = error;
        Swal.fire('Error', error, 'error');
      });
  }

  onDelete(user) {
    Swal.fire({
      title: 'Confirm delete',
      text: 'Do you want to delete the selected user\'s record?',
      type: 'question',
      cancelButtonText: 'Cancel',
      showCancelButton: true,
      confirmButtonText: 'Yes Delete',
      showConfirmButton: true
    }).then(willDelete => {
      if (willDelete.value) {
        this.userDataService.deleteUser(user.id).subscribe((status: boolean) => {
            if (status) {
              Swal.fire('Deleted!', 'User\'s record deleted successfully!', 'success');
              this.internalUsers.splice(this.internalUsers.indexOf(user), 1);
            }
            this.loading = false;
          }, (err) => {
            Constants.handleError(err, this.router, true);
            this.loading = false;
          }
        );
      }
    })
  }

  public toggle(type) {
    this.type = type;
  }

}
