import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ICity, IState, IVendor} from '../../../interfaces/interfaces';
import {VendorDataService} from '../../../services/vendor-data.service';
import {UserDataService} from '../../../services/user.service';
import {ActivatedRoute, Router} from '@angular/router';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../data';

@Component({
  selector: 'app-edit-vendor',
  templateUrl: './edit-vendor.component.html',
  styleUrls: ['./edit-vendor.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class EditVendorComponent implements OnInit {

  @Input() user: any = null;

  public vendorForm: FormGroup;
  errorMessage: any;
  model: any = {};
  states: IState[];
  cities: ICity[];
  loading: boolean;
  lockLoading: boolean;
  pageLoading: boolean;
  showHide: boolean;
  onSuccess: any;

  vendor: IVendor = null;
  userData: any = false;
  isCurrentUser = false;


  constructor(private vendorDataService: VendorDataService,
              private userDataService: UserDataService,
              private route: ActivatedRoute,
              private formBuilder: FormBuilder,
              private router: Router) {
  }

  ngOnInit() {
    let user_id = this.route.snapshot.params['id'];
    if (this.user && this.user.id) {
      user_id = this.user.id;
    }

    const user = AuthService.getAuthenticatedUser();
    this.isCurrentUser = user && (user_id.toString() === user.i.toString());

    this.buildForm();
    this.getVendor(user_id);
    this.showHide = false;
  }

  can(permission) {
    return AuthService.can(permission);
  }

  get permissions() {
    return AppData.permissions;
  }

  buildForm() {
    this.vendorForm = this.formBuilder.group({
      business_name: ['', Validators.required],
      rc_number: ['', Validators.required],
      tax_identification_number: ['', Validators.required],
      business_description: ['', Validators.required],
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      email: ['', Validators.compose([Validators.required, Validators.email])],
      email1: [''],
      website: [''],
      phone: ['', Validators.compose([Validators.required, Validators.minLength(8)])],
      username: ['', Validators.required],
      password: ['', Validators.compose([Validators.required, Validators.minLength(8)])],
      address_line_1: ['', Validators.required],
      state_id: ['', Validators.required],
      city_id: ['', Validators.required],
      address_line_2: [''],
      pobox_number: ['']
    })
  }

  getVendor(user_id: number) {
    this.pageLoading = true;
    this.vendorDataService.getVendorByID(user_id).subscribe((data: any) => {
      this.vendor = data.user.vendor;
      this.userData = data.user;
      this.states = data.states;
      this.pageLoading = true;
      this.vendorForm.patchValue({
        first_name: this.userData.first_name,
        last_name: this.userData.last_name,
        email: this.userData.email,
        email1: this.vendor.email1,
        website: this.vendor.website,
        phone: this.vendor.phone,
        username: this.userData.username,
        password: 'Password',
        business_name: this.vendor.business_name,
        rc_number: this.vendor.rc_number,
        tax_identification_number: this.vendor.tax_identification_number,
        business_description: this.vendor.business_description,
        address_line_1: this.vendor.address_line_1,
        address_line_2: this.vendor.address_line_2,
        state_id: this.vendor.state_id,
        city_id: this.vendor.city_id,
        pobox_number: this.vendor.pobox_number
      });

      this.changeCities(this.vendor.state_id);
      this.pageLoading = false
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
      this.pageLoading = false;
    });
  }

  changeCities(value) {
    this.cities = [];
    if (value) {
      for (let x = 0; x < this.states.length; x++) {
        if (this.states[x].id.toString() === value.toString()) {
          this.cities = this.states[x].cities;
          break;
        }
      }
    }
  }

  onSubmit({value, valid}: { value: IVendor, valid: boolean }) {
    value.user_id = this.vendor.user_id;
    this.loading = true;
    this.errorMessage = '';
    if (value.user_id) {
      this.vendorDataService.updateVendor(value).subscribe((data: any) => {
        if (data) {
          Swal.fire('Successful', 'Data updated successfully', 'success');
        }
        this.loading = false;
      }, (err) => {
        Constants.handleError(err, this.router, true);
        this.loading = false;
      })
    }
  }

  toggleLock() {
    this.showHide = !this.showHide;
  }

  onDelete() {
    Swal.fire({
      title: 'Confirm delete',
      text: 'Do you want to delete this record?',
      type: 'question',
      cancelButtonText: 'Cancel',
      showCancelButton: true,
      confirmButtonText: 'Yes Delete',
      showConfirmButton: true
    }).then(willDelete => {
      if (willDelete.value) {
        this.userDataService.deleteUser(this.vendor.user_id).subscribe((status: boolean) => {
          if (status) {
            Swal.fire('Deleted!', 'User profile deleted successfully!', 'success');
            this.router.navigate(['/app/vendors/list']);
          } else {
            Swal.fire('Sorry', 'Unable to delete user', 'error');
          }
        }, (err) => {
          Constants.handleError(err, this.router, true)
        })
      }
    })
  }

  onLock() {
    this.lockLoading = true;
    this.userDataService.lockUser(this.vendor.user_id, this.model.reason).subscribe((reason) => {
      if (reason) {
        Swal.fire('Account Status!', 'User account status changed!', 'success');
        this.router.navigate(['/app/vendors/list']);
      }
      this.lockLoading = false;
    }, (err) => {
      Swal.fire('Error', Constants.handleError(err, this.router), 'error');
      this.lockLoading = false;
    });
  }

}
