import {RouterModule, Routes} from '@angular/router';
import {ModuleWithProviders} from '@angular/core';
import {PagesComponent} from './pages.component';
import {SearchComponent} from './search/search.component';
import {NotificationsComponent} from './notifications/notifications.component';
import {MyProfileComponent} from './my-profile/my-profile.component';

export const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {path: '', redirectTo: 'dashboard', pathMatch: 'full'},
      {
        path: 'dashboard',
        loadChildren: () => import('app/pages/dashboard/dashboard.module').then(m => m.DashboardModule),
        data: {breadcrumb: 'Dashboard'}
      },
      {
        path: 'users',
        loadChildren: () => import('app/pages/users/user-management.module').then(m => m.UserManagementModule),
        data: {breadcrumb: 'Users'}
      },
      {
        path: 'vendors',
        loadChildren: () => import('app/pages/vendors/vendors.module').then(m => m.VendorsModule),
        data: {breadcrumb: 'Vendors'}
      },
      {
        path: 'roles',
        loadChildren: () => import('app/pages/roles/roles.module').then(m => m.RolesModule),
        data: {breadcrumb: 'Roles'}
      },
      {
        path: 'ministries',
        loadChildren: () => import('app/pages/institution/institution.module').then(m => m.InstitutionModule),
        data: {breadcrumb: 'Ministries'}
      },
      {
        path: 'goods',
        loadChildren: () => import('app/pages/good/good.module').then(m => m.GoodModule),
        data: {breadcrumb: 'Goods'}
      },
      {
        path: 'goods-categories',
        loadChildren: () => import('app/pages/category/category.module').then(m => m.CategoryModule),
        data: {breadcrumb: 'Goods Categories'}
      },
      {
        path: 'warehouses',
        loadChildren: () => import('app/pages/warehouses/warehouses.module').then(m => m.WarehousesModule),
        data: {breadcrumb: 'Warehouses'}
      },
      {
        path: 'vendors/goods',
        loadChildren: () => import('app/pages/vendor-goods/vendor-goods.module').then(m => m.VendorGoodsModule),
        data: {breadcrumb: 'Vendor Goods'}
      },
      {
        path: 'faqs',
        loadChildren: () => import('app/pages/faq/faq.module').then(m => m.FaqModule),
        data: {breadcrumb: 'FAQs'}
      },
      {
        path: 'feedbacks',
        loadChildren: () => import('app/pages/feedbacks/feedbacks.module').then(m => m.FeedbacksModule),
        data: {breadcrumb: 'Feedbacks'}
      },
      {
        path: 'system-logs',
        loadChildren: () => import('app/pages/system-logs/system-logs.module').then(m => m.SystemLogsModule),
        data: {breadcrumb: 'System Logs'}
      },
      {
        path: 'survey',
        loadChildren: () => import('app/pages/survey/main-survey.module').then(m => m.MainSurveyModule),
        data: {breadcrumb: 'Survey'}
      },
      {
        path: 'document-formats',
        loadChildren: () => import('app/pages/documents/document-formats/document-formats.module').then(m => m.DocumentFormatsModule),
        data: {breadcrumb: 'Document Formats'}
      },
      {
        path: 'document-types',
        loadChildren: () => import('app/pages/documents/document-types/document-types.module').then(m => m.DocumentTypesModule),
        data: {breadcrumb: 'Document Typess'}
      },
      {
        path: 'workflow',
        loadChildren: () => import('app/pages/workflow/workflow.module').then(m => m.WorkflowModule),
        data: {breadcrumb: 'Workflow'}
      },
      {
        path: 'grades',
        loadChildren: () => import('app/pages/grade/grade.module').then(m => m.GradeModule),
        data: {breadcrumb: 'Grades'}
      },
      {
        path: 'message-templates',
        loadChildren: () => import('app/pages/message-templates/message-templates.module').then(m => m.MessageTemplatesModule),
        data: {breadcrumb: 'Message Templates'}
      },
      {path: 'chat', loadChildren: () => import('app/pages/chat/chat.module').then(m => m.ChatModule)},
      {path: 'search', component: SearchComponent, data: {breadcrumb: 'Search'}},
      {path: 'me', component: MyProfileComponent, data: {breadcrumb: 'My profile'}},
      {path: 'notifications', component: NotificationsComponent, data: {breadcrumb: 'Notifications'}},
    ]
  }
];

export const routing: ModuleWithProviders = RouterModule.forChild(routes);
