import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {VendorDataService} from '../../../services/vendor-data.service';
import {UserDataService} from '../../../services/user.service';
import {Router} from '@angular/router';
import {IVendor, PaginatedLog} from '../../../interfaces/interfaces';
import {AuthService} from '../../../services/auth.service';
import {AppData} from '../../../data';

@Component({
    selector: 'app-vendors-list',
    templateUrl: './vendors-list.component.html',
    styleUrls: ['./vendors-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class VendorsListComponent implements OnInit {

    vendors: IVendor[] = [];
    loading: boolean;
    role = localStorage.getItem('userRole');
    errorMessage = '';
    type = 'list';
    pVendors: PaginatedLog;
    from: number;
    current_page: number;
    last_page: number;
    total: number;
    to: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    searchableList = ['first_name', 'last_name', 'business_name', 'email'];
    queryString: '';
    model: any = {};
    create_vendors: boolean;
    update_vendors: boolean;
    delete_vendors: boolean;


    constructor(private vendorDataService: VendorDataService,
                private userDataService: UserDataService,
                private router: Router) {
    }

    ngOnInit() {
        this.submitSearch();
    }

    submitSearch(perPage: any = 15) {
        this.loading = true;
        let fromDate;
        let toDate;
        if (this.model.hasOwnProperty('from')) {
            fromDate = this.model.from.year + '-' + this.model.from.month + '-' + this.model.from.day
        }
        if (this.model.hasOwnProperty('to')) {
            toDate = this.model.to.year + '-' + this.model.to.month + '-' + this.model.to.day
        }

        const searchParam = [];
        if (this.queryString) {
            searchParam.push('search=' + this.queryString);
        }

        if (fromDate) {
            searchParam.push('from=' + fromDate);
        }
        if (perPage) {
            searchParam.push('per-page=' + perPage);
        }

        if (searchParam.length) {
            this.searchUsers('', searchParam.join('&'));
        } else {
            this.searchUsers('', '');
        }
    }

    searchUsers(url, params?) {
        this.vendors = [];
        this.next_page_url = null;
        this.prev_page_url = null;
        this.last_page = -1;
        this.loading = true;
        this.vendorDataService.searchVendors(url, params).subscribe((data: any) => {
            this.vendors = data.data;
            this.current_page = data.current_page;
            this.from = data.from;
            this.last_page = data.last_page;
            this.next_page_url = data.next_page_url;
            this.path = data.path;
            this.prev_page_url = data.prev_page_url;
            this.per_page = data.per_page;
            this.to = data.to;
            this.total = data.total;
            this.loading = false;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.loading = false;
        });
    }

    onDelete(vendor, i) {
        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to delete this user\'s record?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.loading = true;
                this.userDataService.deleteUser(vendor.user_id).subscribe((status: boolean) => {
                    if (status) {
                        Swal.fire('Deleted!', 'User profile deleted successfully!', 'success');
                        this.vendors.splice(i, 1);
                    }
                    this.loading = false;
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                    this.loading = false;
                });
            }
        })
    }

    public toggle(type) {
        this.type = type;
    }

    can(permission) {
        return AuthService.can(permission);
    }

    get permissions() {
        return AppData.permissions;
    }

}
